@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
body {
  background: #eeedeb;
  font-family: 'Source Sans Pro', sans-serif;
  margin: auto;
  overflow-x: hidden;
  overflow-y: hidden;
}

.scroll {
  overflow-y: scroll;
}

#barMessage {
  background: #f79646;
  border-radius: 7px 7px 0 0;
  /* color: #fff; */
  color: #333;
  margin: auto;
  margin-bottom: 1px;
  padding: 20px;
  width: 896px;
}

/**
* This class is added to
* .choice container when about is selected
*  to hide the overlay
* to elements of barMessage according to home or about path
**/
.hide {
  display: none;
}

/**
*  This rule applies a white border to all images except to the flag images
*    in the overlay
**/
img:not(#flag):not(#emoji) {
  background: #48b145;/* correct the app icons probably shape are not regular */
  border: 5px solid #fff;
  border-radius: 15px;
}

@media screen and (max-width: 411px) {

  #barMessage {
    width: 260px;
  }

}

@media screen and (min-width: 765px) and (max-width: 801px) {

  #barMessage {
    /* width: 700px; */
    width: 752px;
    font-size: 1.1em;
    margin: 0 10px;
  }

}

.overlay {
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.8);
  height: 100%;
  top: 68%;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 40px 45px;
  position: fixed;
  width: 100%;
}

.emptyOverlay {
  background-color: rgb(200,200,200);
  background-color: rgba(175,175,175,0.5);
  /* display: block; */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 45px;
  position: fixed;
}

#landingMessage {
  color: #fff;
  font-size: 1.1em;
}

#landingMessage h4 {
  font-weight: normal;
  margin: 18px 0;
}

#getInformed,
#agreeButton {
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 1.05em;
  padding: 15px 32px;
  -webkit-transition:all 0.3s ease-out;
  transition:all 0.3s ease-out;
}

.langButton {
  background: #292929;
  border: 2px solid #fff;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-size: 1.05em;
  padding: 12px 20px;
}

.pulse {
  -webkit-animation: pulse 1s infinite linear;
          animation: pulse 1s infinite linear;
  display: inline-block;
  position: relative;
}

@-webkit-keyframes pulse {
  0% { -webkit-transform:scale(0.9); transform:scale(0.9); }
  50% { -webkit-transform:scale(1.2); transform:scale(1.2); }
  100% { -webkit-transform:scale(0.9); transform:scale(0.9); }
}

@keyframes pulse {
  0% { -webkit-transform:scale(0.9); transform:scale(0.9); }
  50% { -webkit-transform:scale(1.2); transform:scale(1.2); }
  100% { -webkit-transform:scale(0.9); transform:scale(0.9); }
}

#getInformed:hover,
#agreeButton:hover {
  -webkit-transform:scale(1.2);
          transform:scale(1.2);
}

#getInformed {
  border: 3px solid #d42929;
  background: #292929;
  margin-right: 30px;
}

#agreeButton {
  border: 3px solid #039403;
  background: #292929;
  margin-right: 30px;
}

#getInformed:hover {
  background: #f33;
}

#agreeButton:hover {
  background: #04c104;
}

#select {
  border-radius: 5px;
  font-size: 1em;
  padding: 5px;
}

.langSwitch {
  display: flex;
}

#flag {
  border-radius: 8px;
  margin-left: 25px;
  margin-top: 7px;
}

@media screen and (max-width: 411px) {

  #landingMessage h4 {
    max-width: 90%
  }

  .overlay {
    padding: 20px;
    top: 53%
  }

  #getInformed,
  #agreeButton {
    padding: 15px 7px;
  }

}

.nav-group {
