.modal {
  background: #2f2f2f;
  box-shadow: 8px 8px 6px 0px #807e7e;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  height: 260px;
  width: 500px;
  left: 50%;
  margin-left: -250px;
  margin-top: -150px;
  position: fixed;
  top: 50%;
}

.modal h2 {
  color: #fff;
  margin-left: 1em;
  margin-bottom: 2em;
  width: 400px;
}

.closeModal {
  border: none;
  border-radius: 2px;
  bottom: -10%;
  cursor: pointer;
  font-size: 1.3em;
  left: 65%;
  padding: 13px 24px;
  position: relative;
  width: 7em;
}

.emptyDialog {
  background: rgb(161,160,160);
  background: -webkit-radial-gradient(circle, rgba(161,160,160,1) 38%, rgba(119,119,119,1) 78%);
  background: radial-gradient(circle, rgba(161,160,160,1) 38%, rgba(119,119,119,1) 78%);
  display: none;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 1;
}

.show {
  display: block;
}

.list {
  background: #2f2f2f;
  color: #fff;
  border: 3px solid #fff;
  border-radius: 1px;
  cursor: pointer;
  font-size: 1.3em;
  padding: 15px;
  margin: 0em auto;
  width: 11em;
}

.selectionContainer a:link,
.selectionContainer a:visited {
  color: #333;
  text-decoration: none;
}

.choice {
  /* color: #fff; */
  color: #333;
  display: flex;
  flex-flow: column;
}

.selectionContainer {
  background: #f79646;
  border-radius: 0 0 7px 7px;
  display: flex;
  margin: auto;
}

/**
* This class is added to .selectionContainer to remove rounded
*   borders when an app is selected.
**/
.fixBorder {
  border-radius: 0;
}

.appSelection {
  align-items: center;
  display: flex;
  flex-flow: column;
  padding: 20px;
  -webkit-transition:all 0.3s ease-out;
  transition:all 0.3s ease-out;
  width: 194px;
}

.appSelection:hover {
  background: #e38639;
  border-radius: 7px;
  cursor: pointer;
  -webkit-transform:scale(1.1);
          transform:scale(1.1);
}

@media screen and (max-width: 411px) {

  img {
    height: 40px;
    width: 40px;
  }

  .selectionContainer {
    flex-wrap: wrap;
  }

  .appSelection {
    padding: 12px;
    width: 125px;
  }

  .selectionContainer {
    width: 300px;
  }

}

@media screen and (min-width: 765px) and (max-width: 801px) {

  .appSelection {
    width: 164px;
    /* width: 145px; */
  }

  .appSelection p {
    font-size: 1.1em;
  }

  .choice {
    width: 812px;
  }

  .selectionContainer {
    margin: 0 10px;
  }

}

